import {useForm} from 'react-hook-form';
import Button from './components/Button';

interface FieldsProps {
  fields: {
    seatNumber: number;
    userName: string;
    checkIn: string;
    checkOut: string;
  }
}

function ReservationForm({fields, onChange, onSubmit}: any) {
  const {
    register,
    handleSubmit,
    formState: {errors},
  } = useForm();

  console.log(errors);

  const {
    seatNumber,
    userName,
    checkIn,
    checkOut,
  } = fields;

  function handleChange(event: any) {
    const {target: {name, value}} = event;
    onChange({name, value});
  }

  return (
    <section>
      <form
        onSubmit={handleSubmit((data) => {
          console.log(data);
        })}
      >

        <label htmlFor="register-seatNumber">
           
        </label>
        <input
          {...register('seatNumber', {required: '   '})}
          placeholder=" "
          name="seatNumber"
          value={seatNumber}
          onChange={handleChange}
        />

        <label htmlFor="register-name">
          
        </label>
        <input
          {...register('userName', {required: '  '})}
          placeholder=""
          name="userName"
          value={userName}
          onChange={handleChange}
        />

        <label htmlFor="register-checkIn">
           
        </label>
        <input
          {...register('checkIn', {required: '    '})}
          placeholder="  (hh:mm)"
          name="checkIn"
          value={checkIn}
          onChange={handleChange}
        />

        <label htmlFor="register-checkOut">
           
        </label>
        <input
          {...register('checkOut', {required: '    '})}
          placeholder="  (hh:mm)"
          name="checkOut"
          value={checkOut}
          onChange={handleChange}
        />

        <Button
          onClick={onSubmit}
        >
          
        </Button>
      </form>
    </section>
  );
}

export default ReservationForm;
